import Loading from '../../public/Loading'
import LoadingImg from '../../public/loadingImg'
import None from '../../public/noNumber'
  export default {
      name: "palyerInvite",
    data(){
        return{
          api:this.GLOBAL.baseURL,
          value:'',
          userImg:'../../../static/img/static/user.png',
          options:[
            {label:'全部',value:'0'},
            {label:'待处理',value:'1'},
            {label:'已同意',value:'2'},
            {label:'已谢绝',value:'3'},
          ],
          palyerList:[],
          total:0,
          isLoading:false
        }
    },
    components:{
      None,
      LoadingImg,
      Loading
    },
    methods:{
      toPublish(item,index){
        this.$router.push({
          path: '/publishPage',
          query:{
            information: item.activity_id
          }
        })
      },
        //选择类型
      getOther(val){
        if(val != 0){
          this.getOtherInvit(1,parseInt(val)-1)
        }else{
          this.getInvite(1)
        }
      },
        //同意或拒绝
      deelInvite(item,a){
        this.$confirm('此操作将同意或者拒绝该申请','是否继续？','提示',{
          confirmButtonText:'确定',
          cancelButtonText:'取消',
          type: 'warning'
        })
          .then(()=>{
            this.goOndelte(item,a)
          })
          .catch(()=>{
            this.$message({
              type: 'info',
              message: '已取消'
            })
          })
      },
      goOndelte(item,a){
        this.$http.post(this.api + '/InviteA',{
          token: localStorage.getItem('token'),
          invite_id: item.invite_id,
          flag: a
        })
          .then(res=>{
            if(res.data.code == 1){
              if(a == 1){
                this.$message({
                  type: 'success',
                  message: '同意成功！'
                })
              }else{
                this.$message({
                  type: 'success',
                  message: '谢绝成功！'
                })
              }
              item.audit = a
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.goOndelte(item,a)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getOtherInvit(val,autid){
        this.isLoading = true
        this.$http.post(this.api + '/InviteL',{
          token: localStorage.getItem('token'),
          page:val,
          audit:autid
        })
          .then(res=>{
            if(res.data.code == 1){
              if(res.data.data.data){
                this.palyerList = res.data.data.data
                this.total = res.data.data.total
              }else{
                this.palyerList = []
                this.total = 0
              }
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getInvite(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
            }
          })
      },
        getInvite(val){
        this.isLoading = true
          this.$http.post(this.api + '/InviteL',{
            token: localStorage.getItem('token'),
            page:val
          })
            .then(res=>{
              if(res.data.code == 1){
                if(res.data.data.data){
                  this.palyerList = res.data.data.data
                  this.total = res.data.data.total
                }else{
                  this.palyerList = []
                  this.total = 0
                }
                this.isLoading = false
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.getInvite(val)
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
                this.isLoading = false
              }
            })
        },
      handleCurrentChange(val){
        if(this.value){
          this.getOtherInvit(val,parseInt(this.value) - 1)
        }else{
          this.getInvite(val)
        }
      },
      touser(item,index){
        this.$router.push({
          path: '/userdata',
          query: {
            information: item.invuser.user_id
          }
        })
      }
    },
    mounted() {
        this.getInvite(1)
    }
  }



